import React, { Component } from 'react';
import './css/calendar.css';

class Calendar extends Component {
    // CLIENT_ID = '312368892805-4ch2kp41si8hb9908vs79k39roh9a8gg.apps.googleusercontent.com'
    // API_KEY ='AIzaSyBpe3RescOypZACkPup0sqEhGHU_h8-nus'
    // "https://www.googleapis.com/discovery/v1/apis/calendar/v3/rest"
    

    constructor(props){
        super(props);
        this.state = {
            today: this.props.selectedDate,
            year: this.props.selectedDate.getFullYear(),
            month: this.props.selectedDate.getMonth(),
            day: this.props.selectedDate.getDate(),
            dateSet:[],
        }
        console.log(this.state);
        this.days=[31,28,31,30,31,30,31,31,30,31,30,31];
        this.date=[];
        this.monthNames = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
        //this.dataSource = new RestDataSource(`https://www.googleapis.com/discovery/v1/apis/calendar/v3/rest?API_KEY=AIzaSyBpe3RescOypZACkPup0sqEhGHU_h8-nus&CLIENT_ID=312368892805-4ch2kp41si8hb9908vs79k39roh9a8gg.apps.googleusercontent.com`, (err) => this.props.history.push(`/components/error/${err}`));
        
    }

    componentWillReceiveProps(nextProps) {
        this.setState({ today: nextProps.selectedDate,
                        year: nextProps.selectedDate.getFullYear(),
                        month: nextProps.selectedDate.getMonth(),
                        day: nextProps.selectedDate.getDate() });  
    }

    componentDidMount(){
        /* day: Sun ~ Sat(0~6)
            : 4 400  , 100    
            -1 1 1 
            -2 1 1 
            -3 1 1 
            -4 1 1 
            -5 1 1 (04  +1  .)
            - 1 1  : ((year-1)+((year-1)/4)-((year-1)/100)+((year-1)/400)+1)%7
            -2 1: ((year-1) + ((year-1)/4)-((year-1)/100)+((year-1)/400)+1 +      2  1  31/ 3  1 2  ))%7
        */ 
         this.checkYear();
    }

    checkYear = () => {
        if(((Number(this.state.year) % 4 === 0) && !(this.state.year % 100 === 0)) || (this.state.year % 400 === 0)){
            let days = [...this.days];
            days[1] = 29;
            this.days = days;
        }
        this.checkWeek();
    }

    checkWeek = () => {
        let weekday = (this.state.year-1)+Math.floor((this.state.year-1)/4)-Math.floor((this.state.year-1)/100)+Math.floor((this.state.year-1)/400)+1;
        let temp = 0;
        for(let i=0; i<this.state.month; i++){
            temp += this.days[i];
        }

        weekday += temp;
        weekday %= 7;
        for(let j=0; j<weekday; j++){
            this.date.push('');
        }
        
        for(let z=1; z <= this.days[this.state.month]; z++){
            this.date.push(z);
        }
        this.setState({dateSet: this.date});
    }

    prevCalendar = () =>{
        let prevMonth = Number(this.state.month)-1
        this.date = [];
        prevMonth < 0 ? this.setState({ month:11, year: this.state.year-1}, this.checkYear) : this.setState({ month: prevMonth}, this.checkYear)
    }

    nextCalendar = () =>{
        let prevMonth = Number(this.state.month)+1
        this.date = [];
        prevMonth > 11 ? this.setState({ month:0, year: this.state.year+1}, this.checkYear) : this.setState({ month: prevMonth}, this.checkYear)
    }

    clickHandler = (day) => {
        // console.log(day);
        // this.setState({day: day}, () => this.props.callback(this.state.day+"/"+(this.state.month+1)+"/"+this.state.year));
        this.props.callback(day+"/0"+(this.state.month+1)+"/"+this.state.year);
    }
    render() {
        return (
            <div className="container">
                <div className="month">      
                    <ul>
                        <li className="prev" onClick={this.prevCalendar}>&#10094;</li>
                        <li className="next" onClick={this.nextCalendar}>&#10095;</li>
                        <li>
                            {`${this.monthNames[this.state.month]} ${this.state.year}`}
                        </li>
                    </ul>
                </div>

                <ul className="weekdays">
                    <li>Mo</li>
                    <li>Tu</li>
                    <li>We</li>
                    <li>Th</li>
                    <li>Fr</li>
                    <li>Sa</li>
                    <li>Su</li>
                </ul>

                <ul className="days">
                    {this.state.dateSet.map((p,index) => 
                        Number(this.state.day) === Number(p) && this.state.month === this.props.selectedDate.getMonth()? <li key={index}><span className="active" style={{cursor:'pointer'}} onClick={() => this.clickHandler(p)}> {p}</span></li> : {p} !== '' ? <li key={index}  style={{cursor:'pointer'}} onClick={() => this.clickHandler(p)}> {p}</li> : <li key={index}> <br/></li>
                    )}
                </ul>
            </div>
        );
    }
}

export default Calendar;