import React, { Component } from 'react';
import { RestDataSource } from "../../webservice/RestDataSource";
import noImage from "../../img/noImage.jpg"
import { Link } from 'react-router-dom';

class News extends Component {    
    constructor(props){
        super(props);
        this.state = {
            news:[],
            showDetails: false,
        }
        this.dataSource = new RestDataSource(`https://newsapi.org/v2/everything?q=bitcoin&from=2020-02-18&sortBy=publishedAt&apiKey=7fb2453ddf4745778220ffac8863abfa`, (err) => this.props.history.push(`/components/error/${err}`));
        // this.dataSource = new RestDataSource(`https://api.nytimes.com/svc/mostpopular/v2/viewed/1.json?api-key=bifZRWA9mAZYGpkd3ODKFKDn5qGWXTsZ`, (err) => this.props.history.push(`/components/error/${err}`));
        this.detailNews = {}
    }
    
    componentDidMount(){
        this.dataSource.GetData(data => this.reverseData(data.articles));
    }

    reverseData = (data) => {
        console.log(data);
        this.setState({news: data});
    }

    handleOnClick = (news) => {
        this.detailNews = news;
        this.setState({showDetails: true});
        console.log(this.detailNews);
    }

    render() {
        return (
            <React.Fragment>
                <h2 className="border-bottom my-3">News for Bitcoin</h2>
                <div className="row">
                    {this.state.news.map((p, index) => (
                        <div className="col-sm-3" key={index}>
                            <Link className="list-group-item text-secondary"  to={{ pathname: '/news/'+index, state:{ newsDetail: p} }}>
                                <h5 className="overtext">{p.title}</h5>
                                {/* <img src={p.media[0].mediaMetadata[0].url} alt="" className="img-thumbnail"/> */}
                                {p.urlToImage === null ? <img src={noImage} alt="" className="img-thumbnail"/> : <img src={p.urlToImage} alt="" className="img-thumbnail"/>}
                                <p className="overtext">By {p.author}</p>
                                {<p>{Intl.DateTimeFormat('en-GB', {year: "numeric", month: "long", day: "2-digit"}).format(new Date(p.publishedAt))}</p> }
                            </Link>
                        </div>
                    ))}
                </div>
            </React.Fragment>
        );
    }
}

export default News;