import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import DropdownMenu from '../DropdownMenu';

class Navbar extends Component {

    constructor(props){
        super(props);
        this.state = {
          location:[
            {
                id:0,
                to: '/components/board',
                title: 'Board',
                selected: false,
                key:'location'
            },
            {
                id:1,
                to: '/components/boardform',
                title: 'Board Form',
                selected: false,
                key:'location'
            },
            {
                id:2,
                to: '/components/formValidate',
                title: 'Form Validate',
                selected: false,
                key:'location'
            },
            {
                id:3,
                to: '/components/navtab',
                title: 'Navigation Tab',
                selected: false,
                key:'location'
            },
            {
                id:4,
                to: '/components/carousel',
                title: 'Carousel',
                selected: false,
                key:'location'
            },
            {
                id:5,
                to: '/components/isolated',
                title: 'Restful Ex',
                selected: false,
                key:'location'
            },
            {
                id:6,
                to: '/components/weatherInfo',
                title: 'Weather API',
                selected: false,
                key:'location'
            },
            {
                id:7,
                to: '/components/calendarDrop',
                title: 'Dropdown Calendar',
                selected: false,
                key:'location'
            }
          ],
          navToggle:true,
          navToggleText:''
        }
    }

    handleClickToggleIcon = () => {
        this.setState(prevState => (prevState.navToggle ? {navToggleText:"show", navToggle: !prevState.navToggle } : {navToggleText:"", navToggle: !prevState.navToggle}));
    }

    render() {
        return (
            <div>
                <nav className="navbar navbar-expand-sm navbar-dark bg-dark">
                    <Link className="navbar-brand nav-a-link" to={'/'}>Ben's Playground</Link>
                    <button className="navbar-toggler" type="button" onClick={this.handleClickToggleIcon}>
                        <span className="navbar-toggler-icon"></span>
                    </button>

                    <div className={`collapse navbar-collapse ${this.state.navToggleText}`}>
                        <ul className="navbar-nav mr-auto">
                            <li className="nav-item">
                                <Link className="nav-link" to={'/'}>Home</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to={'/about'}>About</Link>
                            </li>
                            {/* <li className="nav-item component-hidden">
                                <Link className="nav-link" to={'/components'}>Components</Link>
                            </li> */}
                            <li className="nav-item">
                                <DropdownMenu title="Components" list={this.state.location}/>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to={'/weatherapi'}>Weather API</Link>
                            </li>
                            {/* <li className="nav-item">
                                <Link className="nav-link" to={'/contact'}>Contact</Link>
                            </li> */}
                            {/* <li className="nav-item">
                                <Link className="nav-link" to={'/board'}>Board</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to={'/formValidate'}>Form Validate</Link>
                            </li> */}
                            {/* <DropdownMenu title="Select location" list={this.state.location}/> */}
                        </ul>
                    </div>
                </nav>
            </div>
        );
    }
}

export default Navbar;