import React, { Component } from 'react';
import Calendar from './Calendar';

class CalendarDrop extends Component {

    constructor(props){
        super(props);
        this.state={
            today: new Date(),
            fromDateValue:"",
            toDateValue:"",
            showDropdownFrom:false,
            showDropdownTo:false
        }
    }

    clickHandler = (event) => {
        if(event.target.id === "checkIn1"){
            this.setState({
                showDropdownFrom: true,
                showDropdownTo:false
            });
        }else{
            this.setState({
                showDropdownFrom: false,
                showDropdownTo: true
            });
        }
    }

    onChangeHandler = () => {
        console.log("come");
    }

    getSelectedDate = (date) => {
        console.log(this.state.showDropdownFrom);
        if(this.state.showDropdownFrom){
            this.setState({ today: date, fromDateValue : date, toDateValue: date, showDropdownFrom: false, showDropdownTo:true});
        }else{
            this.setState({ toDateValue : date, showDropdownTo:false});
        }
    }

    render() {
        const today = this.state.today;
        let partsFromDate = this.state.fromDateValue.split("/");
        let partsToDate = this.state.toDateValue.split("/");
        const fromDateValue = this.state.fromDateValue === "" ? today : new Date(partsFromDate[2], partsFromDate[1]-1, partsFromDate[0]);
        const toDateValue = this.state.toDateValue === "" ? this.state.today: new Date(partsToDate[2], partsToDate[1]-1, partsToDate[0]);
        console.log(toDateValue);
        return (
            <div>
                <div className="row">
                    <div className="col-sm-2">
                        <div className="dropdown">
                            <div className="form-group">
                                <label>From Date</label>
                                <input type="text" className="form-control" id="checkIn1" value={this.state.fromDateValue} placeholder="DD/MM/YYYY" onChange={this.onChangeHandler} onClick={this.clickHandler}/>
                            </div>
                            <div className={`dropdown-menu ${this.state.showDropdownFrom === true ? 'show': ''}`} style={{width: '276px'}}>
                                <Calendar name="check1" selectedDate={fromDateValue} callback={this.getSelectedDate}/>
                            </div>
                        </div>
                    </div>
                    <div className="col-sm-2">
                        <div className="dropdown">
                            <div className="form-group">
                                <label>To Date</label>
                                <input type="text" className="form-control" id="checkIn2" value={this.state.toDateValue} placeholder="DD/MM/YYYY" onChange={this.onChangeHandler} onClick={this.clickHandler}/>
                            </div>
                            <div className={`dropdown-menu ${this.state.showDropdownTo === true ? 'show': ''}`} style={{width: '276px'}}>
                                <Calendar name="check2" selectedDate={toDateValue} callback={this.getSelectedDate}/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default CalendarDrop;