import React, { Component } from 'react';
import noImage from "../../img/noImage.jpg"
import test from "../../img/test.JPG"

class NewsDetails extends Component {
    constructor(props){
        super(props);
        this.viewData = this.props.location.state.newsDetail;
        // console.log(this.viewData);
    }

    componentDidMount = () =>{
        window.scroll(0, 0);
    }

    // scrollStep() {
    //     if (window.pageYOffset === 0) {
    //         clearInterval(this.state.intervalId);
    //     }
    //     window.scroll(0, window.pageYOffset - this.props.scrollStepInPx);
    //   }
      
    //   scrollToTop() {
    //     let intervalId = setInterval(this.scrollStep.bind(this), this.props.delayInMs);
    //     this.setState({ intervalId: intervalId });
    //   }

    render() {
        return (
            <div className="container margin-top-30">
                <div className="row">
                     <div className="col-sm-12">
                         <div className="main-post">
                             <h3 className="border-bottom my-3">{this.viewData.title}</h3>
                             <p className="post-author">{`By ${this.viewData.author} | Published At ${Intl.DateTimeFormat('en-GB', {year: "numeric", month: "long", day: "2-digit"}).format(new Date(this.viewData.publishedAt))}`}</p>
                             {/* {<img src={test} alt="" className="mw-100"/>} */}
                             {this.viewData.urlToImage === null ? <img src={noImage} alt="" className="mw-100"/> : <img src={this.viewData.urlToImage} alt="" className="rounded mw-100"/>}
                             <p>{this.viewData.content}</p>
                         </div>
                     </div>
                </div>
            </div>
        );
    }
}

export default NewsDetails;